<template><section><h1>Usage</h1>
<h2>Usage</h2>
<h3>Global Registration</h3>
<p>Regist all components of <strong>vue-cesium</strong> at once.</p>
<ul>
<li>Use the default Cesium library url</li>
</ul>
<pre v-pre><code v-pre class="language-javascript"><span class="hljs-keyword">import</span> Vue <span class="hljs-keyword">from</span> <span class="hljs-string">'vue'</span>
<span class="hljs-keyword">import</span> VueCesium <span class="hljs-keyword">from</span> <span class="hljs-string">'vue-cesium'</span>
<span class="hljs-comment">// VueCesium will load Cesium.js from `https://unpkg.com/cesium/Build/Cesium/Cesium.js` by default.</span>
Vue.use(VueCesium)
</code></pre>
<ul>
<li>Specify the Cesium library url and the CesiumIon online resource accessToken:</li>
</ul>
<pre v-pre><code v-pre class="language-javascript"><span class="hljs-keyword">import</span> Vue <span class="hljs-keyword">from</span> <span class="hljs-string">'vue'</span>
<span class="hljs-keyword">import</span> VueCesium <span class="hljs-keyword">from</span> <span class="hljs-string">'vue-cesium'</span>

Vue.use(VueCesium, {
  <span class="hljs-comment">// cesiumPath is path of Cesium.js', for example:</span>
  <span class="hljs-comment">// local Cesium Build package:</span>
  <span class="hljs-comment">// cesiumPath: /static/Cesium/Cesium.js</span>
  <span class="hljs-comment">// Personal online Cesium Build package：</span>
  <span class="hljs-comment">// cesiumPath: 'https://zouyaoji.top/vue-cesium/statics/Cesium/Cesium.js'</span>
  <span class="hljs-comment">// Personal online SuperMap Cesium Build package：</span>
  <span class="hljs-comment">// cesiumPath: 'https://zouyaoji.top/vue-cesium/statics/SuperMapCesium/Cesium.js'</span>
  <span class="hljs-comment">// Official Online Cesium Build package：</span>
  <span class="hljs-attr">cesiumPath</span>: <span class="hljs-string">'https://unpkg.com/cesium/Build/Cesium/Cesium.js'</span>,
  <span class="hljs-comment">// Cesium.Ion.defaultAccessToken</span>
  <span class="hljs-attr">accessToken</span>: <span class="hljs-string">''</span>
})
</code></pre>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-layer-imagery</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-layer-imagery</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
  <span class="hljs-selector-class">.viewer</span> {
    <span class="hljs-attribute">width</span>: <span class="hljs-number">100%</span>;
    <span class="hljs-attribute">height</span>: <span class="hljs-number">400px</span>;
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>
</code></pre>
<h3>Local Registration</h3>
<p>If you need to introduce components on demand, you can choose to locally register Cesium components, which will reduce the size of the package after the project is packaged.</p>
<ul>
<li>Use the default Cesium library url</li>
</ul>
<pre v-pre><code v-pre class="language-javascript"><span class="hljs-keyword">import</span> Vue <span class="hljs-keyword">from</span> <span class="hljs-string">'vue'</span>
<span class="hljs-keyword">import</span> { Viewer, ImageryLayer } <span class="hljs-keyword">from</span> <span class="hljs-string">'vue-cesium'</span>
<span class="hljs-comment">// VueCesium 默认使用 `https://unpkg.com/cesium/Build/Cesium/Cesium.js`</span>
Vue.use(Viewer)
Vue.use(ImageryLayer)
</code></pre>
<ul>
<li>Specify the Cesium library url and the CesiumIon online resource accessToken:</li>
</ul>
<pre v-pre><code v-pre class="language-javascript"><span class="hljs-keyword">import</span> Vue <span class="hljs-keyword">from</span> <span class="hljs-string">'vue'</span>
<span class="hljs-keyword">import</span> { Viewer, ImageryLayer } <span class="hljs-keyword">from</span> <span class="hljs-string">'vue-cesium'</span>

Vue.use(Viewer, {
  <span class="hljs-comment">// cesiumPath is path of Cesium.js', for example:</span>
  <span class="hljs-comment">// local Cesium Build package:</span>
  <span class="hljs-comment">// cesiumPath: /static/Cesium/Cesium.js</span>
  <span class="hljs-comment">// Personal online Cesium Build package：</span>
  <span class="hljs-comment">// cesiumPath: 'https://zouyaoji.top/vue-cesium/statics/Cesium/Cesium.js'</span>
  <span class="hljs-comment">// Personal online SuperMap Cesium Build package：</span>
  <span class="hljs-comment">// cesiumPath: 'https://zouyaoji.top/vue-cesium/statics/SuperMapCesium/Cesium.js'</span>
  <span class="hljs-comment">// Official Online Cesium Build package：</span>
  <span class="hljs-attr">cesiumPath</span>: <span class="hljs-string">'https://unpkg.com/cesium/Build/Cesium/Cesium.js'</span>,
  <span class="hljs-comment">// Cesium.Ion.defaultAccessToken</span>
  <span class="hljs-attr">accessToken</span>: <span class="hljs-string">''</span>
})
Vue.use(ImageryLayer)
</code></pre>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-layer-imagery</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-layer-imagery</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
  <span class="hljs-selector-class">.viewer</span> {
    <span class="hljs-attribute">width</span>: <span class="hljs-number">100%</span>;
    <span class="hljs-attribute">height</span>: <span class="hljs-number">400px</span>;
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>
</code></pre>
<h3>CDN Registration</h3>
<pre v-pre><code v-pre class="language-js">&lt;!-- include Vue --&gt;
<span class="xml"><span class="hljs-tag">&lt;<span class="hljs-name">script</span> <span class="hljs-attr">src</span>=<span class="hljs-string">"https://unpkg.com/vue@latest/dist/vue.min.js"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span></span>
&lt;!-- include VueCesium --&gt;
<span class="xml"><span class="hljs-tag">&lt;<span class="hljs-name">script</span> <span class="hljs-attr">src</span>=<span class="hljs-string">"https://unpkg.com/vue-cesium@latest/lib/index.umd.js"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span></span>
<span class="xml"><span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="actionscript">
  Vue.use(VueCesium) <span class="hljs-comment">// All VueCesium components now globally installed</span>
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span></span>
</code></pre>
<p><a href="https://zouyaoji.top/vue-cesium/statics/umd.html">UMD DEMO</a></p>
