        <span class="hljs-attr">animation</span>: <span class="hljs-literal">false</span>
      }
    },
    mounted() {
      <span class="hljs-keyword">this</span>.$refs.viewer.createPromise.then(<span class="hljs-function">(<span class="hljs-params">{Cesium, viewer}</span>) =&gt;</span> {
