        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
        <span class="hljs-comment">// Get Cesium and viewer instances here, then execute the relevant logic code</span>
        <span class="hljs-keyword">this</span>.camera.position.lng = <span class="hljs-number">116.46</span>
        <span class="hljs-keyword">this</span>.camera.position.lat = <span class="hljs-number">39.92</span>
